/* ===== Base Reset ===== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  line-height: 1.6;
  color: #333;
  background: #f8f9fa;
  padding: 0;
}

main.container {
  padding: 0 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

h1,
h2,
h3 {
  font-weight: 600;
  color: #222;
  margin-bottom: 1rem;
}

p {
  margin-bottom: 1rem;

}

a {
  text-decoration: none;
  color: inherit;
}

img {
  width: 100%;
  border-radius: 10px;
  margin-right: 20rem;
}

.line {
  height: 0.01rem;
  width: 70rem;
  background-color: #222;
  margin-bottom: 5rem;
}

.linee {
  height: 0.01rem;
  width: 70rem;
  background-color: #222;
  margin-top: 5rem;
}

.treat {
  display: flex;
  justify-content: space-evenly;
  margin-top: 5rem;
  width: 100%;
}

.each {
  width: 60%;
}

.each p {
  text-align: start;
  line-height: 1.3rem;
}

.pic {
  width: 40%;
}

.pic img {
  margin-top: 3rem;
}

.colabo {
  margin-top: 5rem;
}

.title {
  display: flex;
  justify-content: space-around;
  margin-top: 5rem;
}

.one {
  margin: 2rem;
}

.test img {
  height: 10rem;
  width: 10rem;
  border-radius: 50%;
  object-fit: cover;
  margin: 2rem;
}

.test {
  display: flex;
  flex-direction: row;
  margin: 5rem;

}




/* ===== Header ===== */
/* Header */
.header {
  text-align: center;
  padding: 3rem 1rem;
  background: linear-gradient(to right, skyblue, lightgreen);
}

.header img {
  max-width: 110px;
  margin-bottom: 0.5rem;
  border-radius: 50%;
  background: #fff;
  padding: 8px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
}

.header h1 {
  font-size: 2.5rem;
  color: #fff;
}

.tagline {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  color: #f1f1f1;

}

.phones {
  font-size: 1rem;
  margin-bottom: 1rem;
  color: #fff;
  font-weight: 500;
}

/* ===== Buttons ===== */
.btn {
  display: inline-block;
  background: #0077b6;
  color: #fff;
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  transition: background 0.3s ease;
}

.btn:hover {
  background: #023e8a;
}

.btn.small {
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
}

/* ===== Navbar ===== */
.navbar {
  width: 100%;
  height: 8rem;
  background: url('https://res.cloudinary.com/dspsj31q8/image/upload/v1760255966/d15219cd-0ecc-44e3-ac97-742ecd0c29b5_uvkpmm.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  position: sticky;
  top: 0;
  z-index: 999;
}

.nav-container {
  height: 100%;
  max-width: 1200px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem 1.5rem;
}

.navbar .logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #fff;
  text-decoration: none;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 1.5rem;
  transition: max-height 0.3s ease-in-out;
  justify-content: center;
  align-items: center;
}

.nav-links li a {
  text-decoration: none;
  color: #fff;
  font-weight: 500;
  transition: color 0.3s;
}

.nav-links li a:hover {
  color: #ffd60a;
}

/* Dropdown styles */
.dropdown {
  position: relative;
}

.dropdown-content {
  display: none;
  position: absolute;
  background: #0077b6;
  min-width: 200px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 1;
  top: 100%;
  left: 0;
}

.dropdown-content li {
  list-style: none;
}

.dropdown-content li a {
  padding: 12px 16px;
  display: block;
  color: #fff;
  text-decoration: none;
}

.dropdown-content li a:hover {
  background: #023e8a;
}

.dropdown:hover .dropdown-content {
  display: block;
}

/* Book button inside nav */
.nav-links .btn.small {
  background: #ffd60a;
  color: #333;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  transition: 0.3s;
}

.nav-links .btn.small:hover {
  background: #ffc300;
}

/* Hamburger button (hidden on desktop) */
.menu-toggle {
  display: none;
  background: none;
  border: none;
  font-size: 1.8rem;
  color: #fff;
  cursor: pointer;
}

/* ===== Responsive ===== */
@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }

  .nav-links {
    position: absolute;
    top: 60px;
    right: 0;
    background: #0077b6;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    max-height: 0;
    overflow: hidden;
    padding: 0;
  }

  .nav-links.show {
    max-height: 500px;
    /* enough to show links */
    padding: 1rem;
  }

  .nav-links li {
    width: 100%;
    margin: 0.5rem 0;
  }

  .nav-links li a {
    display: block;
    width: 100%;
    padding: 0.8rem;
  }
}


/* ===== Container & Sections ===== */
.container {
  margin: auto;
}

section {
  padding: 3rem 0;
  text-align: center;
}

/* ===== Vision & Mission ===== */
.vision-mission {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
}

.vision-card {
  background: #fff;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s;
}

.vision-card:hover {
  transform: translateY(-5px);
}

/* ===== Services & Team Cards ===== */
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}

.card {
  background: #fff;
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover {
  transform: translateY(-8px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
}

.card h3 {
  margin: 0.8rem 0;
}

/* ===== News & Insights ===== */
.news-hero {
  background: linear-gradient(135deg, #a8ea66 0%, #03691e 100%);
  color: #fff;
  padding: 6rem 0;
  text-align: center;
}

.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  font-weight: 700;
}

.hero-content p {
  font-size: 1.3rem;
  max-width: 600px;
  margin: 0 auto;
  opacity: 0.9;
}

.news-grid {
  padding: 4rem 0;
  background: #f8f9fa;
}

.news-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}

.news-card {
  background: #fff;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.news-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15);
}

.news-image {
  position: relative;
  height: 200px;
  overflow: hidden;
}

.news-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.news-card:hover .news-image img {
  transform: scale(1.1);
}

.news-category {
  position: absolute;
  top: 15px;
  left: 15px;
  background: #0077b6;
  color: #fff;
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
}

.news-content {
  padding: 1.5rem;
}

.news-meta {
  margin-bottom: 0.5rem;
}

.news-date {
  color: #666;
  font-size: 0.9rem;
}

.news-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 0.8rem;
  line-height: 1.4;
}

.news-excerpt {
  color: #666;
  line-height: 1.6;
  margin-bottom: 1rem;
}

.news-read-more {
  color: #0077b6;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease;
}

.news-read-more:hover {
  color: #023e8a;
}

/* ===== Newsletter Signup ===== */
.newsletter-signup {
  background: linear-gradient(135deg, #0077b6 0%, #023e8a 100%);
  color: #fff;
  padding: 4rem 0;
  text-align: center;
}

.newsletter-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 0 2rem;
}

.newsletter-container h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.newsletter-container p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  opacity: 0.9;
}

.newsletter-form {
  display: flex;
  gap: 1rem;
  max-width: 500px;
  margin: 0 auto;
}

.newsletter-form input {
  flex: 1;
  padding: 1rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
}

.newsletter-form .btn {
  padding: 1rem 2rem;
  white-space: nowrap;
}

/* ===== Services Page ===== */
.services-hero {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #fff;
  padding: 6rem 0;
  text-align: center;
}

.services-overview {
  padding: 4rem 0;
  background: #f8f9fa;
}

.overview-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  text-align: center;
}

.overview-container h2 {
  font-size: 2.5rem;
  margin-bottom: 3rem;
  color: #333;
}

.overview-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.overview-item {
  background: #fff;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.overview-item:hover {
  transform: translateY(-5px);
}

.overview-icon {
  font-size: 3rem;
  color: #0077b6;
  margin-bottom: 1rem;
}

.overview-item h3 {
  font-size: 1.3rem;
  margin-bottom: 1rem;
  color: #333;
}

.overview-item p {
  color: #666;
  line-height: 1.6;
}

/* ===== Contact Section ===== */
.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-top: 2rem;
}

.contact-info {
  text-align: left;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 0.9rem 1rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-family: inherit;
}

.contact-form input:focus,
.contact-form textarea:focus {
  border-color: #0077b6;
  outline: none;
  box-shadow: 0 0 6px rgba(0, 119, 182, 0.3);
}

/* ===== Footer ===== */
.footer {
  background: #023e8a;
  color: #fff;
  text-align: center;
  padding: 1.5rem 0;
}

.correction {
  display: flex;
  justify-content: space-around;
  margin: 5rem 0 0 0;
}

.nav {
  margin-left: -8rem;
}

.logo img {
  height: 10rem;
  width: 10rem;
  object-fit: cover;
  margin: 0 5rem;
}

.correction ul {
  display: flex;
  flex-direction: column;
}

.footer .social-icons h4 {
  margin-bottom: 3rem;
}

.social-icons .new h4 {
  margin: 2rem 0;
}

.social-icons .new input {
  height: 3rem;
  width: 15rem;
  background-color: #023e8a;
  border: solid 2px #fff;
}

.social-icons .new button {
  width: 5rem;
  height: 2rem;
  border-radius: 5px;
  color: #023e8a;
  background-color: #fff;
  border: solid 2px #023e8a;
  font-size: 10px;
  margin: 20px;
}

.footer .social-icons a {
  color: #fff;
  margin: 20px 10px;
  font-size: 1.2rem;
  transition: color 0.3s;
}

.line {
  width: 90%;
  height: 2px;
  background-color: #fff;
  margin: 3rem 5rem;
}

.footer .social-icons a:hover {
  color: #ffd60a;
}

.pragraphy p {
  margin: 3rem 1rem 0 0;
}

/* ===== Modals ===== */
.modal {
  display: none;
  position: fixed;
  z-index: 2000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-content {
  background: #fff;
  margin: 10% auto;
  padding: 2rem;
  border-radius: 12px;
  max-width: 600px;
  text-align: left;
  position: relative;
  animation: fadeIn 0.4s ease;
}

.modal .close {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 1.5rem;
  color: #333;
  cursor: pointer;
}

@keyframes fadeIn {
  from {
    
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===== Animations ===== */
.fade-in {
  
  transform: translateY(30px);
  transition: opacity 0.8s ease, transform 0.8s ease;
}

.fade-in.appear {
  opacity: 1;
  transform: translateY(0);
}

/* ===== Booking Page Styles ===== */
.booking-section {
  padding: 4rem 0;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  min-height: 80vh;
}

.booking-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 2rem;
  text-align: center;
}

.booking-container h1 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 0.5rem;
}

.booking-subtitle {
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 3rem;
}

.booking-form {
  background: #fff;
  padding: 3rem;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  text-align: left;
}

.form-group {
  margin-bottom: 2rem;
}

.form-row {
  display: flex;
  gap: 2rem;
  margin-bottom: 2rem;
}

.form-row .form-group {
  flex: 1;
  margin-bottom: 0;
}

.form-group label {
  display: block;
  font-weight: 600;
  color: #333;
  margin-bottom: 0.5rem;
  font-size: 1rem;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 1rem;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  font-size: 1rem;
  font-family: inherit;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #0077b6;
  box-shadow: 0 0 0 3px rgba(0, 119, 182, 0.1);
}

.form-group select {
  cursor: pointer;
}

.form-group textarea {
  resize: vertical;
  min-height: 100px;
}

.booking-submit {
  width: 100%;
  padding: 1.2rem;
  font-size: 1.2rem;
  font-weight: 600;
  background: linear-gradient(135deg, #0077b6 0%, #023e8a 100%);
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.booking-submit:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 119, 182, 0.3);
}

/* ===== Responsive ===== */
@media (max-width: 768px) {
  main.container {
    padding: 0 1rem;
  }

  .contact-container {
    grid-template-columns: 1fr;
  }

  .navbar ul {
    flex-direction: column;
  }

  .navbar li {
    margin: 0.8rem 0;
  }

  .correction {
    flex-direction: column;
    align-items: center;
  }

  .correction .nav,
  .correction .contact-info {
    margin-top: 1rem;
  }

  .treat {
    flex-direction: column;
  }

  .each {
    width: 100%;
  }

  .pic {
    width: 100%;
  }

  .colabo .title {
    flex-direction: column;
  }

  .one {
    margin: 1rem 0;
  }

  .booking-container {
    padding: 0 1rem;
  }

  .booking-form {
    padding: 2rem;
  }

  .form-row {
    flex-direction: column;
    gap: 0;
  }

  .booking-container h1 {
    font-size: 2rem;
  }

  .news-container {
    grid-template-columns: 1fr;
    padding: 0 1rem;
  }

  .hero-content h1 {
    font-size: 2.5rem;
  }

  .newsletter-form {
    flex-direction: column;
    gap: 1rem;
  }

  .newsletter-form input {
    width: 100%;
  }

  .overview-grid {
    grid-template-columns: 1fr;
  }

  .overview-container {
    padding: 0 1rem;
  }

  .services-hero .hero-content h1 {
    font-size: 2.5rem;
  }
}

/* Slide section */
.slide {
  background: url('https://res.cloudinary.com/dspsj31q8/image/upload/v1760255966/d15219cd-0ecc-44e3-ac97-742ecd0c29b5_uvkpmm.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  text-align: center;
  padding: 0;
}